.presentation {
  padding: 0 20px;

  @media (min-width: $tablet-width) {
    padding: 0 60px;
  }

  @media (min-width: $desktop-width) {
    padding: 0 200px;
  }
}

.presentation__title {
  line-height: 26px;
  font-size: 18px;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
  padding-top: 17px;
  margin-bottom: 30px;

  @media (min-width: $tablet-width) {
    padding-top: 58px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 62px;
  }
}

.presentation__text {
  text-align: center;
  text-transform: uppercase;
  font-weight: normal;
  margin: 0;
  margin-bottom: 38px;

  @media (min-width: $tablet-width) {
    margin-bottom: 66px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 64px;
  }
}

.presentation__video {
  width: 100%;
  min-height: 177px;
  background-color: #3b6689;

  @media (min-width: $tablet-width) {
    min-height: 500px;
  }
}

.presentation__wrapper {
  position: relative;
}

.presentation__button {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 48px;
  height: 54px;
  background-image: url("../img/icon-play-button.svg");
  background-repeat: no-repeat;
  translate: -50% -50%;
  border: none;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
}
